.popup__container {
  width: 100%;
  max-width: 430px;
  padding: 34px 36px 37px;
  box-sizing: border-box;
  position: relative;
  font-family: 'Inter', Arial, Helvetica;
  background-color: #FFFFFF;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
}

@media screen and (max-width: 768px) {
  .popup__container {
    padding: 25px 22px;
  }  
}